.arrow-button {
  display: flex;
  align-items: center;
  background: var(--brand-blue);
  color: var(--white);
  margin-top: 0.8rem;
  max-width: 14rem;
  max-width: 13.6rem;
  padding: 1.4rem 1.6rem;
}

.arrow-button p {
  margin-right: 0.8rem;
}

/* MEDIA QUERIES for screens (768, 426)*/

@media screen and (max-width: 768px) {
  .arrow-button {
    padding: 1rem;
    max-width: 12.4rem;
  }
}

@media screen and (max-width: 426px) {
  .arrow-button {
    padding: 0.9rem 1.2rem;
    max-width: 12.8rem;
  }
}
